</template>

<script>
export default {
  name: 'Title',
  props:{
      icon:String

  },
  data(){
    return{

    }
  },
  methods:{

  }
}
</script>

<style lang="stylus">
@import 'assets/stylus/base'
.icon-txt
  color: $text-light
  display: flex
  align-items: center
  justify-content: flex-start
  font-size: $font-sm
  .icon
    font-size: 17px
    color: inherit
    margin-right: 3px
    line-height: 1
    text-align: center
  .txt
    display: inline-block
    line-height: 1
    font-size: 12px
  & + &
    margin-left: 15px

</style>



