<template>
    <ul class="cell-group" :style="{marginTop : mt + 'px',marginBottom: mb + 'px'}">
      <li class="group-title" v-if="title">{{title}}</li>
      <slot></slot>
    </ul>
</template>

<script>
export default {
  props:{
      title : {
          type:String,
          default: ''
      },
      mt : {
          type:String,
          default: '0'
      },
      mb : {
          type:String,
          default: '0'
      }

  },
  data(){
    return{

    }
  },
  methods:{

  }
}
</script>

<style lang="stylus">
@import 'assets/stylus/base'
.cell-group
  display: block
  list-style: none
  // @include fine-border(top,$form-border);
  .group-title
    padding: 10px $spacing
    color: $text-grey
    font-size: $font-sm

</style>



