<template>
  <div class="list">
    <slot></slot>
    <div class="empty" v-if="isEmpty">
      <div class="empty-box">
        <img src="src/assets/img/empty.png" />
      </div>
      <div class="info-text">空空如也:(</div>

    </div>

  </div>
</template>

<script>
export default {
  name: 'List',
  props:{
    isEmpty : {
      type : Boolean,
      default : false
    }
  },
  data(){
    return{

    }
  },
  methods:{

  }
}
</script>

<style lang="stylus">
@import 'assets/stylus/base'
.list
  .list-footer
    background: #fff
    position: relative
    color: $text-grey
    font-size: 14px
    text-align: center
    padding: 10px
    fine-border-two(tb, $border-light)
  .group
    margin-top: 10px
    .item-header
      background: #fff
      padding: 0 $spacing
      height: 44px
      fine-border(bottom, $border-light)
      display: flex
      align-items: center
      justify-content: space-between
      .left
        span
          font-size: 12px
          color: $text-light
.empty
  text-align: center;
  padding: 40px $spacing $spacing;
  .empty-box
    margin-bottom: 10px;

    img
      width: 100px;
      display: inline-block;
      opacity: .5
  .info-text
    font-size: 16px;
    color: #c8c8c8;
</style>



